.missionLinks{
  width: 100vw;
  background: url(../../assets/rocket.png) no-repeat center center fixed;
  background-size: cover;
  height: 1000px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
}

.missionLinks__text{
  font-size: 40px;
  letter-spacing: 30px;
  font-weight: 600;
  color: var(--white)
}

.missionLinks__buttonRow{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 40px;
}

.buttonRound{
  border-radius: 25px;
  width: 250px;
  height: 45px;
  margin: 50px 15px;
  background-color: transparent;
  border: 1px solid var(--white);
  color: var(--white);
  font-size: 16px;
}

.buttonRound:focus {outline:0;}

.buttonRound:hover {

    -webkit-box-shadow:inset 0 0 15px var(--white);

            box-shadow:inset 0 0 15px var(--white);
    cursor: pointer;
}



