.list__header__filters{
  height: 80px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 45px;
}

.buttonRound{
  border-radius: 25px;
  width: auto;
  height: 43px;
  margin: 50px 12px;
  background-color: transparent;
  border: 1px solid var(--coolGrey);
  color: var(--coolGrey);
  font-size: 19px;
  padding: 0 36px;
  letter-spacing: 0px;
  font-weight: 400;
}

.buttonRound:focus {outline:0;}

.buttonRound:hover {
  border: 1px solid var(--white);
  -webkit-box-shadow:inset 0 0 15px var(--white);
          box-shadow:inset 0 0 15px var(--white);
  color: var(--white);
  cursor: pointer;
}



